import XButton from 'vux/src/components/x-button/index.vue'

export default {

components: { Group, Cell, XInput, Selector, PopupPicker, Datetime, XButton },

data() {
return {
name: '',
birthday: '',
gender: '',
sex_list: [['男','女']],
sex_val: [],
card: '',
dcard: '',
type_list:[['视力','肢体','精神','智力','言语']],
