}
},

computed: {},

beforeMount () {},

mounted () {
this.$refs.content_box.style.height = document.documentElement.clientHeight - 116 + 'px';
},

methods: {
goBack() {
this.$router.go(-1);
},
submit() {
this.$router.push('/dmd/jy-process')
}
},

watch: {}

}




